*,
*::before,
*::after {
    box-sizing: border-box;
}

body {
    background-color: #F3F3F3;
    margin: 0;
}


/* css for our page */
.container .ql-editor {
    max-width: 8.5in;
    min-height: 11in;
    padding: 1in;
    margin: 1rem;
    box-shadow: 0 0 5px 0 rgba(0, 0, 0, 0.5);
    background-color: white;
    /* border: solid 2px green; */
}


/* css for the container containing our page */
.container .ql-container {
    border: none;
    display: flex;
    /* border: solid 4px black; */
    justify-content: center;
    /* width: 600px; */
    /* border: solid 2px rgb(60, 0, 255); */
}

@media only screen and (max-width: 680px) {
    .container .ql-container {
        width: 600px;
    }
}

@media only screen and (max-width: 560px) {
    .container .ql-container {
        width: 500px;
    }
}

@media only screen and (max-width: 460px) {
    .container .ql-container {
        width: 420px;
    }

    .container .ql-editor {
        padding: 0.5in !important;
        margin: 0.5rem !important;
        background-color: red;
    }
}



/* css for the toolbar */
/* if we use .ql-toolbar.ql-snow then it would depict more specific selection */

.container .ql-toolbar.ql-snow {
    display: flex;
    justify-content: center;
    align-items: center;
    position: sticky;
    top: 0;
    z-index: 1;
    background-color: #F3F3F3;
    border: none;
    box-shadow: 0 0 5px 0 rgba(0, 0, 0, 0.5);
    /* border: solid 2px rgb(0, 246, 25); */
}


@media only screen and (max-width: 460px) {
    .container .ql-toolbar.ql-snow{
        width: 420px !important;
        flex-direction: column;
        justify-content: space-between;
        align-items: flex-start;
    }
}


/* fixing styles for printing the page */
@page {
    margin: 1in;
}

@media print {
    body {
        background: none;
    }

    .container .ql-editor {
        width: 6.5in;
        height: 9in;
        padding: 0;
        margin: 0;
        box-shadow: none;
        align-self: flex-start;
    }


    /* then here also we have to use the specific selection */

    .container>.ql-toolbar.ql-snow {
        display: none;
    }
}

#savedraftbutton {
    /* border: solid 2px red; */
    width: fit-content;
    height: fit-content;
    word-wrap: normal;
    text-align: center;
    padding: 0;
    margin: 0;
    font-size: 0.8rem;
    border-radius: 5px;
    padding: 4px;
    background-color: rgb(65, 66, 65);
    color: white;
}

#savedraftbutton:hover {
    background-color: rgb(115, 117, 115);
}